div.review p.review-text {
    font-size: 1.2rem;
    font-weight: 350;
    font-style: italic;
    margin-bottom: 1rem;
}

div.review img {
    height: 3.9rem;
    position: absolute;
    opacity: 0.16;
}

div.review img.left-quote {
    top: -1.4rem;
    left: -1.2rem;
}

div.review img.right-quote {
    bottom: -0.8rem;
    right: -0.8rem;
}

div.review p.name {
    font-size: 1.5rem;
    font-weight: 600;
}

div.review span {
    margin: 0 1rem;
    font-size: 2.5rem;
    font-weight: 100;
}

div.review p.location {
    text-transform: uppercase;
    font-size: 1.1rem;
    font-weight: 300;
}
